import './App.css';
import API from './api';
import React, { useState, useEffect } from "react";
import moment from "moment";
import "moment/locale/zh-tw";
moment.locale("zh-tw");

function App() {
  const [showDate, setShowDate] = useState(moment().format("YYYY-MM-DD"));
  const [dicFixture, setDicFixture] = useState(null);

  let init = false;

  const dicTeamNameChi = {
    "Arsenal" : "阿仙奴",
    "Aston Villa" : "阿士東維拉",
    "Bournemouth" : "般尼矛夫",
    "Brentford" : "賓福特",
    "Brighton" : "白禮頓",
    "Chelsea" : "車路士",
    "Crystal Palace" : "水晶宮",
    "Everton" : "愛華頓",
    "Fulham" : "富咸",
    "Ipswich" : "葉士域治",
    "Leicester" : "李斯特城",
    "Liverpool" : "利物浦",
    "Manchester City" : "曼城",
    "Manchester United" : "曼聯",
    "Newcastle" : "紐卡素",
    "Nottingham Forest" : "諾定咸森林",
    "Southampton" : "修咸頓",
    "Tottenham" : "熱刺",
    "West Ham" : "韋斯咸",
    "Wolves" : "狼隊",
  } 

  const getData = async () => {
    if (init) {
      return false;
    }
    init = true;
    const form = moment().add(-10, "days").format("YYYY-MM-DD");
    const to = moment().add(10, "days").format("YYYY-MM-DD");
    const api = new API();
    const res = await api.getFixture(form, to);
    const dicData = res.data;
    massageData(dicData);
  };

  const massageData = (dicData) => {
    const arrFixture = dicData['response'];
    const dicFixture = {};
    for (const [i, f] of Object.entries(arrFixture)) {
      const date = f['fixture']['date'].substr(0, 10);
      const time = f['fixture']['date'].substr(11, 5);
      const homeName = f['teams']['home']["name"];
      const homeLogo = f['teams']['home']["logo"];
      const homeGoal = f['goals']['home'];
      const awayGoal = f['goals']['away'];
      const awayName = f['teams']['away']["name"];
      const awayLogo = f['teams']['away']["logo"];
      const dicTemp = {
        date,
        homeName,
        homeLogo,
        homeGoal,
        awayGoal,
        awayName,
        awayLogo,
        time
      }
      if (!dicFixture[date]) {
        dicFixture[date] = [];
      }

      dicFixture[date].push(dicTemp);
    }
    setDicFixture(dicFixture);
  }

  useEffect(() => {
    getData();
  }, [])

  const onClickNext = () => {
    let d = moment(showDate, "YYYY-MM-DD").add(1, "days");
    if (d.format("YYYY-MM-DD") > moment().add(10, "days").format("YYYY-MM-DD")) {
      return false
    }

    setShowDate(d.format("YYYY-MM-DD"));
  }

  const onClickPrevious = () => {
    let d = moment(showDate, "YYYY-MM-DD").add(-1, "days");
    if (d.format("YYYY-MM-DD") < moment().add(-10, "days").format("YYYY-MM-DD")) {
      return false
    }
    setShowDate(d.format("YYYY-MM-DD"));
  }

  const renderFixtureList = () => {
    if (dicFixture == null || !dicFixture[showDate]) {
      return (<div />)
    } else {
      const arrF = dicFixture[showDate];
      console.log(showDate, arrF);
      return (
        <div>
          {arrF.map((f) => renderFixture(f))}
        </div>
      )
    }
  }

  const renderFixture = (fixture) => {

    let strLineThree = fixture.time;
    if (fixture.homeGoal != null) {
      strLineThree = `${fixture.homeGoal}:${fixture.awayGoal}`;
    }

    return (
      <div className='fixtureBox' >
        <div style={{ display: "flex", flexDirection: "row", width: "100%", height: "70px" }}>
          <div className='teamName' >
            <span style={{ fontSize: "5vw" }}>{dicTeamNameChi[fixture.homeName] ? dicTeamNameChi[fixture.homeName] : fixture.homeName}</span>
          </div>
          <div className='teamName'>
            <span style={{ fontSize: "5vw" }}>{dicTeamNameChi[fixture.awayName] ? dicTeamNameChi[fixture.awayName] : fixture.awayName}</span>
          </div>
        </div>
        <div style={{ display: "flex", flexDirection: "row", width: "100%", height: "80px" }}>
          <div className='teamName' >
            <img src={fixture.homeLogo} width="70" height="70" alt=""/>
          </div>
          <div className='teamName'>
            <img src={fixture.awayLogo} width="70" height="70" alt=""/>
          </div>
        </div>
        <div style={{ display: "flex", flexDirection: "row", width: "100%", height: "70px", "justify-content" : "center", "align-items" : "center" }}>
            <span style={{ fontSize: "5vw" }}>{strLineThree}</span>
        </div>
      </div >
    )
  }

  return (
    <div className="App">
      <div style={{ display: "flex", flexDirection: "row" }}>
        <div className='nextButton' style={{ backgroundColor: "aqua" }} onClick={() => onClickPrevious()}>
          <span style={{ fontSize: "6vw" }}>{`<<<<前一日`}</span>
        </div>
        <div className='nextButton' style={{ backgroundColor: "pink" }} onClick={() => onClickNext()}>
          <span style={{ fontSize: "6vw" }}>{`後一日>>>>`}</span>
        </div>
      </div>
      <div className='dateBox' style={{ backgroundColor: "yellow" }}>
        <span style={{ fontSize: "6vw" }}>{moment(showDate, "YYYY-MM-DD").format("YYYY年M月D日")} 星期{moment(showDate, "YYYY-MM-DD").format("dd")}</span>
      </div>
      {(dicFixture == null || !dicFixture[showDate]) && (
        <div className='dateBox' >
          <span style={{ fontSize: "6vw" }}>沒有比賽</span>
        </div>
      )}

      {renderFixtureList()}
    </div>
  );
}

export default App;
