import apisauce from "apisauce";

export default class API {
  constructor(isBuffer) {
    this.api = apisauce.create({
      baseURL: "https://v3.football.api-sports.io/",
      headers: {
        "x-rapidapi-host" : "v3.football.api-sports.io",
        "x-rapidapi-key" : "a87a52d6f59edd5ca0c433674f231b01"
      },
      responseType: isBuffer ? "arraybuffer" : null,
      // responseType: "arraybuffer",
      timeout: 100000,
    });

    this.api.axiosInstance.interceptors.response.use(
      function (response) {
        return response;
      },
      function (error) {
        return Promise.reject(error);
      }
    );
  }


  async getFixture (from, to) {
    const url = `fixtures?season=2024&league=39&timezone=Asia/Hong_Kong&from=${from}&to=${to}`;
    const res = await this.api.get(url);
    return res;
  }
}
